import axios from './axios';
const dataControllers = {
    getYoutube: async (str) => {
        try {
            let result = await axios.get(`search?part=snippet&key=AIzaSyCFPsG14pvT8bTZXQijYJ7h8dH81sxu5LE&q=${str}&maxResults=1000`);
            return result;
        } catch (error) {
            throw error;
        }
    },
}

export default dataControllers;